import { Component, OnInit  } from '@angular/core';
import { slideIn, serviceListIn } from '../../animations/app.animation';

import { Service } from './service';
import { SERVICES } from './services';

@Component({
  selector: 'app-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.css', '../home-page/home-page.component.css'],
  animations: [
    slideIn(),
    serviceListIn()
  ]
})
export class ServicesComponent implements OnInit {
  slideIn = 'hidden';
  serviceListIn = 'hidden';
  Services : Service[]= SERVICES;

  constructor() { }

  isScrolled( receivedStatus: boolean ) {
    if( receivedStatus ){
      this.slideIn = 'shown';
      this.serviceListIn = 'shown';
    }
  }
  
  ngOnInit() {
  }

}
