import { Component, OnInit } from '@angular/core';
import { slideIn, serviceListIn } from '../../animations/app.animation';

@Component({
  selector: 'app-skills',
  templateUrl: './skills.component.html',
  styleUrls: ['./skills.component.css', '../home-page/home-page.component.css'],
  animations: [
    slideIn()
  ]
})
export class SkillsComponent implements OnInit {
  slideIn = 'hidden';

  constructor() { }

  isScrolled( receivedStatus: boolean ) {
    if( receivedStatus ){
      this.slideIn = 'shown';
    }
  }

  ngOnInit() {
  }

}
