import { Directive, ElementRef, HostListener, Output, EventEmitter   } from '@angular/core';
import smoothscroll from 'smoothscroll-polyfill';

@Directive({
  selector: '[appScroll]'
})
export class ScrollDirective {

  constructor(private el: ElementRef) { }

  @Output() isScrolled = new EventEmitter<boolean>();
  @Output() isNotTop = new EventEmitter<boolean>();

  @HostListener('window:scroll', ['$event'])
  checkScroll(event) {
    const elementPosition = this.el.nativeElement.offsetTop;
    const scrollPosition = (window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0) + window.innerHeight * 0.75;

    if (scrollPosition >= elementPosition ) {
      this.isScrolled.emit(true);
    }

    const scrollPositionForShrink = (window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0);
    // console.log('scrollPositionForShrink',scrollPositionForShrink);
    
    if (scrollPositionForShrink >= 100) {
      this.isNotTop.emit(true);
    }else{      
      this.isNotTop.emit(false);
    }
  }

  @HostListener('click', ['$event'])
  onClick(event) {
    let target = event.target || event.srcElement;
    while (target) {
      if (target instanceof HTMLAnchorElement) {
        console.log(target.getAttribute('href'));
        break;
      }
      target = target.parentNode;
    }
    // console.log('event.target.getAttribute href', target.getAttribute('href') );
    if( target ) {
      let linkId = target.getAttribute('href');
      if ( linkId.startsWith("#")) {
        linkId = linkId.substring(1);
      }
      smoothscroll.polyfill();
      window.scroll({
        top: ( document.getElementById( linkId ).offsetTop ) || 0,
        left: 0,
        behavior: 'smooth'
      })
    }
 }
}
