import { Component, OnInit } from '@angular/core';
import { slideIn } from '../../animations/app.animation';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css', '../home-page/home-page.component.css'],
  animations: [
    slideIn()
  ]
})
export class AboutComponent implements OnInit {

  slideIn = 'hidden';

  constructor() { }

  isScrolled( receivedStatus: boolean ) {
    if( receivedStatus ){
      this.slideIn = 'shown';
    }
  }

  ngOnInit() {
  }

}
